body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
    font-size: 16px;
}
html, body, div#root, div#root > div {
    height: 100vh;
}

h1 { font-size: 2.5rem; margin: 0; }
h2 { font-size: 1.25rem; margin: 0; }
h3 { font-size: 1rem; margin: 0; }
button {
    border: none;
    border-radius: 16px;
    cursor: pointer;
    color: white;
    text-transform: uppercase;
    font-weight: bold;
    transition: background-color 0.1s;
    height: 2.5em;
}
button.standard {
    background-color: #e6772d;
    /* background-color: #1a972b; */
    padding: 8px 16px;
    min-width: 5em;
    width: 15em;
}
button.standard:disabled {
    background-color: gray;
    color: lightgray;
    cursor: not-allowed;
}
button.subtle {
    background-color: transparent;
    padding: 4px 4px;
    min-width: 3em;
    color: #e0801a;
    /* color: #1a972b; */
}
button:hover {
    background-color: #ff9d5b;
    /* background-color: #6ae77b; */
    color: white;
}

.App {
    background-color: #6d1717;
    /* background-color: #0f3c2e; */
    text-align: center;
    /* height: 100vmin; */
    /* filter: grayscale(100%); */
    min-width: 360px;
}
.App-background {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0.1;
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);

    background-image: url(/Jammming/static/media/background.89c0c7b8.jpg);
    background-repeat: no-repeat;
    background-size: cover;
}
.App-content-wrapper {
    overflow: hidden;
    position: relative;
    /* height: 90vmin; */
}
.App-content {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 8px;
    color: white;
}
.App-content-section {
    display: flex;
    flex-direction: row;
    width: 90%;

    background-color: rgb(25, 25, 25, 0.75);
    border-radius: 1vmin;
    margin: 1vmin;
    padding: 2vmin;
}
.App-content-section.Startup { justify-content: center; }
.App-content-section.Startup button + button { margin-left: 1em; }
.App-content-section:nth-child(1) { flex: 2 1; }
.App-content-section:nth-child(2) { flex: 1 1; }

@media only screen and (max-width: 700px) {
    html, body, div#root, div#root > div {
        height: auto;
        min-height: 100vh;
    }
    .App-content-section {
        flex-direction: column;
    }
    .App-content-section.Startup { align-items: center; }
    .App-content-section.Startup button + button {
        margin-left: 0;
        margin-top: 1em;
    }
}
.App-Header {
    background-color: rgb(25,25,25);
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    color: white;
    z-index: 1;
    position: relative;
    padding-top: 2vmin;
    padding-bottom: 2vmin;
}
.App-Header h1 .mmm {
    display: inline;
    color: #db814d;
    /* color: #6ae77b; */
}
.App-Header h2 {
    margin-left: 1em;
    font-style: italic;
    color: lightgray;
}
@media only screen and (orientation: portrait) {
    .App-Header {
        flex-direction: column;
    }
    .App-Header h2 {
        margin-left: 0;
        font-size: 1rem;
    }
}
@media only screen and (max-width: 480px) {
    .App-Header h2 {
        font-size: 0.75rem;
    }
}
.SearchBar {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.SearchBar form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    height: 100%;
}
.SearchBar input[type="text"] {
    margin: 8px;
    border-radius: 8px;
    border: none;
    padding: 4px;
    text-align: center;
    background-color: #2d2d2d;
    color: white;
    width: 80%;
    min-width: 20em;
    font-weight: 500;
}
.SearchBar input[type="text"]::-webkit-input-placeholder {
    font-style: italic;
    font-weight: 200;
    color: gray;
}
.SearchBar input[type="text"]::placeholder {
    font-style: italic;
    font-weight: 200;
    color: gray;
}
.SearchBar input[type="text"]:focus {
    background-color: #fbfbfb;
    color: black;
    transition: background-color .5s;
}
.SearchBar button {
    margin-top: 8px;
    width: 50%;
    min-width: 10em;
}
@media only screen and (max-width: 700px) {
    .SearchBar {
        margin-bottom: 2vmin;
    }
}
.Track {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}
.TrackDetails {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 75%;
}
.TrackDetails .TrackTitle {
    font-size: 1rem;
    max-width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.TrackDetails .TrackSubtitle {
    font-size: 0.75rem;
    padding: 0.5em 1em 0.5em 0.5em;
    text-align: left;
    width: 100%;
}
.TrackDetails .TrackSubtitle span + span {
   margin-left: 1em;
}
.TrackDetails .TrackSubtitle .Artist,
.TrackDetails .TrackSubtitle .Album {
    display: inline-block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.TrackDetails .TrackSubtitle .Artist {
    max-width: 50%;
}
.TrackDetails .TrackSubtitle .Album {
    color: gray;
    max-width: 40%;
}
.TrackListContainer {
    padding: 2vmin;
    margin: 0;
    border-radius: 2vmin;
    background-color: #2d2d2d;
    overflow-y: auto;
    overflow-x: clip;
    min-height: 3em;
}
.TrackList {
    list-style-type: none;
    display: flex;
    padding: 0;
    margin: 0;
    height: 100%;
}
.SearchResults .TrackList {
    flex-direction: column;
}
.Playlist .Tracklist {
    flex-direction: row;
}
.SearchResults {
    flex: 2 1;
    max-height: 40vmin;
}
.SearchResults .TrackList {
    /* height: 15em;
    overflow-y: auto; */
}
.SearchResults .Track {
    display: flex;
    flex-direction: row;
    padding: 8px;
    justify-content: space-between;
}
.SearchResults .Track + .Track {
    border-top: 2px solid #3c3c3c;
}
.Playlist {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
}
.Playlist form { flex: 1 1; }
.Playlist input[type="text"] {
    margin: 8px;
    border-radius: 8px;
    border: none;
    padding: 4px;
    text-align: center;
    background-color: #2d2d2d;
    color: white;
    width: 80%;
    min-width: 20em;
    font-weight: 500;
}
.Playlist input[type="text"]::-webkit-input-placeholder {
    font-style: italic;
    font-weight: 200;
    color: gray;
}
.Playlist input[type="text"]::placeholder {
    font-style: italic;
    font-weight: 200;
    color: gray;
}
.Playlist input[type="text"]:focus {
    background-color: #fbfbfb;
    color: black;
    transition: background-color .5s;
}
.Playlist .TrackListContainer {
    flex: 2 1; 
    overflow-x: auto;
}
.Playlist .Track {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 8px;
    min-width: 20em;
    /* min-width: 75%; */
}
.Playlist .Track + .Track {
    border-left: 2px solid #3c3c3c;
}
@media only screen and (max-width: 700px) {
    .Playlist {
        flex-direction: column;
        align-items: normal;
    }
    .Playlist form {
        margin-bottom: 2vmin;
    }
    .Playlist .TrackListContainer {
        /* width: 100%; */
        flex: 2 1;
    }
}
